.icon {
  padding-left: 15px;
}

.nav {
  z-index: 2;
}

@media screen and (min-width: 1024px) {

.container {
    display: flex;
    justify-content: space-between;
    width: 100%
}
  }

@media screen and (min-width: 1500px) {

.container {
    max-width: 1344px;
    width: 1344px;
    margin: auto
}
  }

.title {
  font-size: 20px;
  padding: 0;
  padding-right: 50px;
}

.titleText {
  padding: 0;
}

.titleText:only-child {
  padding-left: 1rem;
}

:global .navbar {
  background-color: auto;
  border-radius: 0;
}

:global .navbar .navbar-menu {
    border-radius: 0;
  }

:global .navbar .navbar-menu.is-active .navbar-item {
    text-align: center;
  }
